import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { getRecipes } from "../../util/contenful";
import "./RecipeContainer.css";

function RecipeContainer({ match }) {
  const [recipes, setRecipes] = useState([]);
  useEffect(() => {
    getRecipes(match.params.category).then(entries => {
      const sortedEntries = entries.items.sort(function (a,b) {
        const valueOfA = a.fields.title.toUpperCase();
        const valueOfB = b.fields.title.toUpperCase();
        if (valueOfA < valueOfB) {
          return -1;
        }
        if (valueOfA > valueOfB) {
          return 1;
        }
        return 0;
      })
      setRecipes(sortedEntries);
    });
  }, [match.params.category]);

  return (
    <div className="container content">
      {recipes.length > 0 ? (
        recipes.map(recipe => (
          <Link
            className="recipe"
            to={"/recipe/" + recipe.sys.id}
            key={recipe.sys.id}
          >
            {recipe.fields.title}
          </Link>
        ))
      ) : (
        <p>Tilføj nogle opskrifter...</p>
      )}
    </div>
  );
}

export default RecipeContainer;
